@media screen and (max-width: 767px) {
  .about {
    .sec1 {
      padding-top: 13.1vw;

      .inner {
        transform: scale(1);
      }

      h1 {
        span {
          font-size: 13.33333333333333vw;
          line-height: 1.32;
          letter-spacing: -0.4vw;

          .noto-font {
            line-height: 1.1;
          }
        }

        &.step2 {
          margin: 0.3vw 0 -0.9vw -0.6vw;

          img {
            width: 64vw;
          }
        }

        &.step3 {
          video,
          .video {
            margin: -1vw 1.9vw 0 0;
            width: 29.333333333333334vw;
          }
        }
        &.step4 {
          margin-top: -0.3vw;

          img {
            margin: -2.8vw 1.8vw 0 2.6vw;
            width: 10.13333333333333vw;
          }
        }
        &.step5 {
          margin-top: 0.5vw;

          img {
            margin: -1.3vw 2.6vw 0 0;
            width: 21.06666666666667vw;
          }
        }
        &.step6 {
          img {
            margin: 1.1vw 0 -2vw;
            width: 53.6vw;
            vertical-align: top;
          }
        }
        &.step7 {
          margin-top: 0.5vw;

          img {
            margin: -2vw 2.1vw 0 2.5vw;
            width: 17.86666666666667vw;
          }
        }
      }
    }

    .sec2 {
      padding-top: calc(var(--vh, 1vh) * 110);

      .marquee {
        font-size: 12vw;
        line-height: 1.1;
        font-weight: 600;
        letter-spacing: -0.303vw;
        animation-duration: 21s;

        i {
          margin-left: 0.5vw;
          letter-spacing: 0;
          font-weight: 500;
        }
      }
    }

    .sec3 {
      .video-box {
        height: 38.13333333333333vw;
      }

      .text-box {
        padding-bottom: 70.33vw;

        .p-box {
          padding-top: 2.6vw;
          display: block;

          p {
            width: 100%;

            span {
              font-size: 5.066666666666667vw;
              line-height: 1.63;
            }

            &.step1 {
              padding-right: 0;
            }

            &.step2 {
              padding: 4.3vw 0 0;
            }
          }
        }

        .scrolling-box {
          margin-top: 61.6vw;

          h3 {
            span {
              font-size: 9.333333333333333vw;
              line-height: 1.43;
              letter-spacing: -0.28vw;
            }
          }
        }
      }
    }

    .sec4 {
      .lists {
        > li {
          padding-bottom: 58vw;

          &:last-child {
            margin-top: -12.5vw;
            padding-bottom: 26.9vw;
          }

          .title-box {
            padding-top: 1.2vw;

            h2 {
              > span {
                font-size: 13.33333333333333vw;
                line-height: 1.25;
                letter-spacing: -0.4vw;

                &::selection {
                  background: #000;
                  color: #fff;
                }
                &::-moz-selection {
                  background: #000;
                  color: #fff;
                }
              }
              a {
                margin: -0.8vw 0 0 0;

                .txt-wrap {
                  font-size: 4vw;
                  line-height: 1.53;

                  .arrow {
                    margin: 0.1vw 0 0 1.5vw;
                    width: 2.549333333333333vw;
                    height: 2.666666666666667vw;
                  }
                }
              }

              &:nth-child(2) {
                margin-top: 0;
              }
            }

            p {
              margin: 8.8vw 0 0 0;

              &.pc {
                display: none !important;
              }
              &.mo {
                display: block !important;
              }

              span {
                font-size: 4.8vw;
                letter-spacing: -0.02vw;
                line-height: 1.64;
              }

              &.step2 {
                margin: 1.3vw 0 0 0;

                span {
                  letter-spacing: 0;
                  line-height: 1.6;
                  font-size: 4.266666666666667vw;
                }
              }
            }
          }

          .circle-lists {
            margin-top: 19vw;

            ol {
              li {
                margin: -8.7vw 0;
                display: block;

                .text-wrap {
                  width: 60.26666666666667vw;
                  height: 60.26666666666667vw;

                  .num {
                    margin-top: 13.7vw;
                    font-size: 4vw;
                  }

                  p {
                    margin-top: 3.4vw;
                    font-size: 4vw;
                    line-height: 1.27;
                    letter-spacing: -0.16vw;
                  }

                  .bottom-txt {
                    margin-top: 2.6vw;
                    font-size: 3.733333333333333vw;
                    letter-spacing: -0.1vw;
                  }
                }
              }
            }
          }

          .earth-lists {
            margin: 12.6vw 0 0.9vw;

            ul {
              display: flex;
              flex-direction: column;
              align-items: center;

              li {
                width: 60.26666666666667vw;
                height: 60.26666666666667vw;

                &:nth-child(1) {
                  margin-right: 0;

                  &:after {
                    top: 84.5%;
                    left: 50%;
                    margin-top: 0;
                    margin-left: -0.5px;
                    z-index: 1;
                  }
                }

                &:nth-child(2) {
                  margin-top: 22.5vw;
                  width: 61.5vw;
                  height: 61.5vw;
                }

                p {
                  > span {
                    font-size: 5.333333333333333vw;
                    line-height: 1.3;
                    letter-spacing: -0.11vw;

                    &:nth-child(1) {
                      margin-top: -2vw;
                    }

                    &:nth-child(2) {
                      margin-top: 2.3vw;
                    }
                  }
                }
              }
            }
          }

          .thank-lists {
            margin-top: 16.9vw;

            > ul {
              > li {
                display: block;

                &:last-child {
                  margin-top: 7.2vw;
                }

                .title {
                  font-size: 5.333333333333333vw;
                  letter-spacing: -0.15vw;
                }

                .sec4-img1 {
                  margin-top: 1.9vw;
                  width: 77.33333333333333vw;
                }

                .li-wrap {
                  > ul {
                    justify-content: center;
                    margin-top: 1.3vw;

                    > li {
                      border-radius: 15.74074074074074vw;

                      &.oval {
                        width: 76vw;
                        height: 30.4vw;
                      }

                      &:not(.oval) {
                        margin-right: -6.2vw;
                        width: 29.38666666666667vw;
                        height: 29.38666666666667vw;

                        &:last-child {
                          margin-right: 0;
                        }
                      }

                      .text-box {
                        padding: 0 7.7vw;
                        font-size: 4vw;
                        letter-spacing: -0.12vw;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }

    .sec5 {
      padding-bottom: 44.6vw;

      h2 {
        padding: 18.9vw 0 0 3.7vw;

        span {
          font-size: 21.33333333333333vw;
          line-height: 0.965;
          letter-spacing: -0.63vw;
        }
      }

      .swiper-wrapper {
        width: 179.3173333333333vw;
      }

      .rolling {
        margin-top: 25.2vw;

        .text-box {
          margin-top: -1.2vw;

          > span {
            font-size: 6.666666666666667vw;
            line-height: 1.28;
            letter-spacing: -0.195vw;

            sup {
              margin: -0.2vw 0 0 1.3vw;
              font-size: 4.3vw;
            }
          }
        }

        .img1 {
          margin: 0.2vw -3vw 0 -3.7vw;
          width: 25.33333333333333vw;
        }
        .img2 {
          margin: -3.5vw 1vw 0 0.1vw;
          width: 63.2vw;
        }
        .img3 {
          margin: -4.5vw 2.2vw 0 2.2vw;
          width: 17vw;
        }
      }

      .p-box {
        margin: 5.6vw 0 0 3.6vw;

        p {
          > span {
            font-size: 4.533333333333333vw;
            line-height: 1.65;
          }
        }
      }
    }

    .sec6 {
      padding-bottom: 48vw;

      .inner {
        padding-bottom: 42.4vw;
      }

      .right {
        &.step1 {
          .img-box {
            margin-top: -22.8vw;
          }

          img {
            width: 44.53333333333333vw;
          }
        }

        &.step2 {
          margin: 3.4vw -1.587301587301587vw 0 0;

          img {
            width: 76.53333333333333vw;
          }
        }

        .wrap {
          display: block;
          padding-right: 1.5vw;
        }

        .text-box {
          padding-top: 3.2vw;

          p {
            span {
              font-size: 4.266666666666667vw;
              line-height: 1.68;
            }
          }
        }
      }

      .title-box {
        position: relative;
        margin-top: -10vw;
        padding-left: 0.3vw;
        z-index: 1;

        h1 {
          span {
            font-size: 12.4vw;
            line-height: 1.13;
            letter-spacing: -0.37vw;

            img {
              top: 1.5vw;
              left: 28vw;
              width: 16.53333333333333vw;
            }
          }

          i {
            margin-top: -2.7vw;
            font-size: 12vw;
            letter-spacing: 0.18vw;
          }
        }
      }
    }
  }
}
