/* common */

body.overflow {
  overflow: hidden;
}
#root {
  position: relative;
}
.pc {
  display: block !important;
}
.mo {
  display: none !important;
}
.inner {
  padding: 0 1.587301587301587vw;
}
.pd-font {
  font-family: "Playfair Display";
}
.noto-font {
  font-family: "Noto Serif KR";
}
.ptd-font {
  font-family: "Pretendard";
}
