/* page transition */
body.overflow .scroll-container {
  height: calc(var(--vh, 1vh) * 100);
}
.routes.black-page {
  background: #000;
}
.routes.fadeIn.active > div {
  opacity: 0;
  animation: fadeIn 0.3s both;
}

.routes.fadeOut > div {
  opacity: 1;
  transition: opacity 1.05s;
}
.routes.fadeOut.active > div {
  opacity: 0;
}

/* arrow animation */
.arrow-txt {
  .arrow {
    position: relative;
    vertical-align: middle;
    display: inline-block;
    margin: -0.2vw 0.7vw 0 1vw;
    width: 2.05026455026455vw;
    height: 2.116402116402116vw;

    span {
      display: block;
      width: 100%;
      height: 100%;
      background: url(../../public/images/next_project_arrow.png) center no-repeat;
      background-size: 100% auto !important;
      transition: opacity 0.3s cubic-bezier(0.215, 0.61, 0.355, 1), transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);

      &.step1 {
        transform: translate3d(0, 0, 0);
        transition-delay: 0.1s;
      }

      &.step2 {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        transform: translate3d(-60%, 60%, 0);
        opacity: 0;
      }
    }
  }

  &:hover {
    .arrow {
      span {
        &.step1 {
          transform: translate3d(60%, -60%, 0);
          opacity: 0;
          transition-delay: 0s;
        }

        &.step2 {
          transform: translate3d(0, 0, 0) rotate(0deg);
          opacity: 1;
          transition-delay: 0.1s;
        }
      }
    }
  }
}

/* work-category-sec */
.work-category-sec {
  /* animation */
  > * {
    opacity: 1 !important;
    transform: none !important;
    transition: none !important;
  }
  h1 span {
    opacity: 0;
    transform: translateY(110%);
    transition: 1.05s cubic-bezier(0.19, 1, 0.22, 1);
  }
  .lb-txt span {
    opacity: 0;
    transform: translateY(110%);
    transition: 1.05s cubic-bezier(0.19, 1, 0.22, 1);
  }
  .p-box p span {
    opacity: 0;
    transform: translateY(110%);
    transition: 1.05s cubic-bezier(0.19, 1, 0.22, 1);
  }
  .video-box {
    video,
    .video {
      opacity: 0;
      transform: scale(1.11);
      transition: 1.05s ease;
    }
  }
  &.ani.active {
    &.not {
      h1 span,
      .lb-txt span,
      .p-box p span {
        transition: none;
      }
    }

    h1 {
      span {
        opacity: 1;
        transform: translateY(0);
      }
      &:nth-child(2) span {
        transition-delay: 0.1s;
      }
      &:nth-child(3) span {
        transition-delay: 0.2s;
      }
    }
    .lb-txt span {
      opacity: 1;
      transform: translateY(0);
      transition-delay: 0.3s;
    }
    .p-box {
      p {
        span {
          opacity: 1;
          transform: translateY(0);
        }
        &:nth-child(1) span {
          transition-delay: 0.4s;
        }
        &:nth-child(2) span {
          transition-delay: 0.5s;
        }
        &:nth-child(3) span {
          transition-delay: 0.6s;
        }
        &:nth-child(4) span {
          transition-delay: 0.7s;
        }
        &:nth-child(5) span {
          transition-delay: 0.8s;
        }
      }
    }
    .video-box {
      video,
      .video {
        opacity: 1;
        transform: scale(1);
      }
    }
  }

  position: relative;

  .p-box {
    p {
      font-size: 0;
      overflow: hidden;
      display: block;

      span {
        display: inline-block;
      }
    }
  }

  .inner {
    position: absolute;
    padding-top: 4.9vw;
    width: 100%;
    z-index: 1;
  }

  h1 {
    margin: -0.22vw 0 -0.7vw;
    font-size: 0;
    display: block;
    overflow: hidden;

    span {
      opacity: 0;
      display: block;
      margin-top: -0.86vw;
      font-size: 6.613756613756614vw;
      line-height: 1.2;
      letter-spacing: -0.2vw;

      i {
        font-family: "Playfair Display";
      }
    }
  }

  .lb-txt {
    margin-top: 0.35vw;
    font-size: 0;
    display: block;
    overflow: hidden;

    span {
      display: inline-block;
      font-size: 1.322751322751323vw;
    }
  }

  .p-box {
    text-align: right;
    margin: 0.9vw 8.2vw 0 0;

    p {
      display: block;
      font-size: 0;
      overflow: hidden;
    }

    span {
      font-family: "Pretendard";
      font-size: 1.322751322751323vw;
      font-weight: 600;
      line-height: 1.6;
    }
  }

  .video-box {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    padding-top: 46.16402116402116%;

    video {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

@media screen and (max-width: 767px) {
  .work-category-sec {
    .inner {
      padding-top: 15.1vw;
    }

    h1 {
      > span {
        font-size: 8vw;
