/* scroll animation */
.ani {
  font-size: 0;
  overflow: hidden;
  display: block;

  &.not {
    > * {
      transition: none;
    }
  }

  > * {
    opacity: 0;
    transform: translateY(110%);
    display: inline-block;
    transition: 1.05s cubic-bezier(0.19, 1, 0.22, 1);
  }
}
.ani.active {
  &.not {
    > * {
      transition: none;
    }
  }

  > * {
    opacity: 1;
    transform: translateY(0);
  }
}
.ani2 {
  font-size: 0;
  overflow: hidden;
  display: block;

  &.not {
    > * {
      transition: none;
    }
  }

  > span,
  > button,
  > div,
  > p,
  > ul {
    opacity: 0;
    transform: translateY(20%);
    display: inline-block;
    transition: 1.05s cubic-bezier(0.19, 1, 0.22, 1);
  }
}
.ani2.active {
  > span,
  > button,
  > div,
  > p,
  > ul {
    opacity: 1;
    transform: translateY(0);
  }
}
.black {
  .line-top {
    &:before {
      background: #fff;
    }
  }
}
.line-top {
  position: relative;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 1px;
    background: #000;
    transition: width 1.05s cubic-bezier(0.19, 1, 0.22, 1);
  }

  &.ani.active {
    &.not {
      > * {
        transition: none;
      }
    }

    &:before {
      width: 100%;
    }
  }
}
.black {
  .line-bottom {
    &:after {
      background: #fff;
    }
  }
}
.line-bottom {
  position: relative;

  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 1px;
    background: #000;
    transition: width 1.05s cubic-bezier(0.19, 1, 0.22, 1);
  }

  &.ani.active {
    &.not {
      > * {
        transition: none;
      }
    }

    &:after {
      width: 100%;
    }
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes posY-110per-up {
  from {
    opacity: 0;
    transform: translateY(110%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes posY-20per-up {
  from {
    opacity: 0;
    transform: translateY(20%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes scale {
  from {
    opacity: 0;
    transform: scale(1.11);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes cover-up {
  0% {
    height: 100%;
  }
  100% {
    height: 0;
  }
}
@keyframes posY-100per-up {
  from {
    opacity: 0;
    transform: translateY(100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes swing-y {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-1.8%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes marquee {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(-100%, 0, 0);
  }
}

/* button animation */
.black {
  .invert-btn {
    background: #000;

    &:before {
      background: #fff;
    }

    .btn-wrap {
      border: 1px solid #fff;
    }
  }
}
.invert-btn {
  position: relative;
  padding: 0.02vw;
  background: #fff;
  border-radius: 1.2vw;
  overflow: hidden;

  &:before {
    content: "";
    position: absolute;
    left: -1px;
    bottom: -1px;
    right: -1px;
    height: 0;
    background: #000;
    transition: 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  }

  &:hover:before {
    height: calc(100% + 2px);
  }

  .btn-wrap {
    display: inline-block;
    border-radius: 1.2vw;
    border: 1px solid #000;

    a {
      mix-blend-mode: difference;
      display: inline-block;
      padding: 0.24vw 0.7vw 0.2vw;
      font-size: 1.19047619047619vw;
      color: #fff;
    }
  }
}
@media screen and (max-width: 767px) {
  .invert-btn {
    padding: 0;
    border-radius: 3.5vw;

    .btn-wrap {
      border-radius: 3.5vw;

      span,
      a {
        padding: 1.7vw 2.6vw 1.2vw;
        font-size: 3.2vw;
      }
    }
  }
}

/* 드래그시 색 반전 */
::selection {
  background: #000;
  color: #fff;
}
::-moz-selection {
  background: #000;
  color: #fff;
}

/* 커서효과 */
html,
body {
  cursor: none !important;
}
*:hover {
  cursor: none !important;
}
button {
  cursor: none !important;
}
a {
  cursor: none !important;
}
body.cursor-default {
  cursor: none !important;

  *:hover {
    cursor: none !important;
  }

  .cursor {
    display: none;
  }
}
.cursor {
  position: fixed;
  top: -1000px;
  left: -1000px;
  width: 20px;
  height: 20px;
  transform: translate(-50%, -50%);
  transform-origin: center;
  border-radius: 50%;
  background: #fff;
  z-index: 100;
  pointer-events: none;
  mix-blend-mode: difference;
  transition: all 150ms ease;
  transition-property: transform;

  &.clicked {
    transform: translate(-50%, -50%) scale(1.3) !important;
  }

  &.hover {
    transform: translate(-50%, -50%) scale(1.9);
    background: #2b79a6;
  }

  &.hidden {
    display: none;
  }
}

