/* vimeo */
.video {
  position: relative;
  width: 100%;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }
}

/* work-creative */
.work-category-sec .video-box {
  .video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    iframe {
      box-sizing: border-box;
      width: calc(1vh * 177.7777777777778);
      width: calc(var(--vh, 1vh) * 177.7777777777778);
      height: 56.25vw;
      min-width: 100%;
      min-height: 100%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

